.App {
  text-align: center;
  margin: 0;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
  transform: translateX(60px);
  transform: translateY(60px);
}

.dash-btn{
  transform: translateX(176px) translateY(-30px);
  width: 300;
  height: 300;
  border-radius: 9px;
  transition: .8s;
}

.continue-btn{
  text-decoration: none;
  color: white;
  transition: 1.2s;

}
