.App-header {
  width: 100%;
  background: linear-gradient(115.87deg, #370617 2.65%, #9D0208 99.74%);
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.App-link {
  color: #FFFF;
}

.wrapper-right {
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.App-header .wrapper-left h1{
font-size: 80px;
line-height: 96.82px;
width:680px;
font-weight: 800;
}
.App-header .wrapper-left p{
font-size: 16px;
font-weight: lighter;
line-height: 30px;
width: 444px;
letter-spacing: -0.5px;
text-align: left;
transform: translateY(5px);


}
.App-header .wrapper-left span{
  margin-right: 9px;
  display: flex;
  transform: translateY(-1px);
}
.App-header .wrapper-left span img{
  margin-right: 10px;
}
.App-header .wrapper-left span p{
  transform: translateY(1px);
}


.wrapper-right>div>button {
  border: none;
  padding: 20px;
  font-size: 16px;
  font-weight: bold;
  margin-top: 200px;
  background-color: #6A040F;
  color: #ffff;
}


.wrapper-left {
  width: 30%;
}

