.wrapper-category {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 20px;
    color: #ffff;
    gap: 30px;
    margin-left: 7px;
}

.wrapper-category div h1{
    margin-left: 72px;
}
.wrapper-category .category-left img{
    margin-left: 63px;
    margin-top: -15px;
    margin-right: -50px;
    height: 200px;
    width: 200px;
}

.category-right {
    width: 100%;
    padding: 40px;
    border-radius: 20px;
    background-color: #ffff;
    margin-top: 48px;
    margin-right: 80px;
    height: 100px;
    transform: translateY(12px);
    
}

.category-right>ul {
    display: flex;
    flex-direction: row;
    list-style-type: none;
    margin-left: -80px;
}


.category-right>ul>li {
    display: flex;
    flex-direction: row;
    list-style-type: none;
    margin-left: 75px;
    padding: 20px;
    border-radius: 10px;
    transform: translateY(-36px);
    box-shadow: 10px 10px 10px 4px rgba(0, 0, 0, 0.45);
    -webkit-box-shadow: 10px 10px 10px 4px rgba(0, 0, 0, 0.45);
    -moz-box-shadow: 10px 10px 10px 4px rgba(0, 0, 0, 0.45);
    
}

