

.wrapper-box {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 20px;
    gap: 30px;
    /* overflow-y: scroll; */
    margin-right: 58px;
}

.box {
    background-color: #ffff;
    border-radius: 10px;
    color: #000000;
    padding: 7px;
    margin-left: -18px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
    height: 240.4571990966797px;
width: 168.65235900878906px;
left: 35.0816650390625px;
top: 7.5427703857421875px;
transition: .8s;


}
.box:hover{
 -webkit-transform: scale(1.1);
}

.box>img{
    width: 150px;
    height: 150px;
    object-fit: none;
}
.box h3{
    margin-top: -10px;
}

.box h2{
    font-weight: bolder;
}

.box h2{
    margin-top: -26px;
}
.rate {
    display: flex;
    flex-direction: row;
    justify-content: left;
    margin-top: -20px;
    margin-bottom: 20px;
    font-weight: bolder;
}


.rate>img {
    margin-right: 10px;
}