.wrapper-header {
    margin: 0;
    padding: 20px;
    color: #ffff;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.wrapper-header>.title {
    width: 20%;
    margin-left: 78px;
    text-decoration: none;
    color: white;
}

.wrapper-search {
    width: 60%;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 74px;
}

.wrapper-search>h3 {
    margin-right: -23px;
}

.search {
    position: relative;
    right: -40px;
    z-index: 1;
}

.wrapper-search>input[type=text] {
    position: relative;
    margin: 5px;
    width: 100%;
    height: 40px;
    border: none;
    padding-left: 40px;
    padding-right: 10px;
    border-radius: 5px;
    background-color: #CE7575;
    font-weight: bold;
    opacity: 70%;

}

.wrapper-search>input[type=text]::placeholder {
    color: #ffff;
    opacity: 1;
}


/* carousel */
.carousel {
    display: flex;
    position: relative;
    overflow: hidden;
    align-items: center;
    justify-content: center;

}

.wrapper-carousel {
    margin-left: 10px;
    margin-right: -90px;
    transform: translateX(50px);
}
